import App from './App.vue'

Vue.config.productionTip = false
// Vue.use(ElementUI)
// Vue.use(VueCodemirror)

Vue.config.errorHandler = function (err, vm) {
  const vueErrorReport = new zaje.VueErrorReport();
  console.log(arguments);
  vueErrorReport.notify(vm, {
    message: err.message,
    stack: err.stack,
  });
}

new Vue({
  render: h => h(App)
}).$mount('#app')
